<template>

    <!--Design para mobile, tablets, ipads, computadores e outros-->
    <v-container fluid v-if="user" :class="{'pt-1': $vuetify.breakpoint.mdAndUp }">

        <v-layout>

            <v-flex xs12 md10 lg8 offset-md1 offset-lg2 class="elevation-5 mt-0">

                <v-layout>
                    <!--Informacao central que nao eh fixa-->
                    <v-flex xs12 class="mb-3">

                        <!--Corpo-->
                        <v-card-text class="white ma-0 pa-0 lighten-5 text-xs-center">

                            <!--Cabecalho de cima.-->
                            <template v-if="showPresentation">
                                <!--Card Azul com a foto-->
                                <v-layout class="ma-0">
                                    <v-flex xs12 class="blue" :style="$vuetify.breakpoint.xsOnly ? 'height:140px;' : 'height:220px'">

                                        <h3 v-if="isMyProfile" style="position: absolute;margin: 20px 10px"
                                            class="white--text">Meu perfil</h3>

                                        <explicador-show-contact-dialog
                                                v-else-if="user"
                                                :user="user"
                                                :onExplicadorTopProfile="true"></explicador-show-contact-dialog>

                                        <v-card
                                            :width="$vuetify.breakpoint.xsOnly ? '50px' : '100px'"
                                            class="card-img-margin"
                                            :style="isMyProfile ? 'margin: 170px auto' : 'margin: 150px auto'">

                                            <a v-if="isUser" :href="user.photoURL" target="_blank" title="Clique para ver a imagem completa">
                                                <v-card-media :src="img" :height="$vuetify.breakpoint.xsOnly ? '50px' : '100px'">
                                                </v-card-media>
                                            </a>

                                            <!--Botao para editar a foto de perfil-->
                                            <v-btn
                                                    v-if="isMyProfile"
                                                    class="edit-photo-icon"
                                                    color="orange"
                                                    icon
                                                    title="Alterar foto de perfil"
                                                    @click.prevent.stop="showDialog = !showDialog">
                                                <v-icon>edit</v-icon>
                                            </v-btn>
                                            <profile-photo-upload v-show="false" :user="user" @closing="fechandoDialog" :showDialog="showDialog"></profile-photo-upload>
                                        </v-card>


                                    </v-flex>
                                </v-layout>

                                <!--Card de baixo-->
                                <v-layout class="red ma-0">

                                    <v-flex xs12 class="white" :style="$vuetify.breakpoint.xsOnly ? 'height:140px;' : 'height:230px'">

                                        <div :style="$vuetify.breakpoint.xsOnly ? 'margin-top: 25px' : 'margin-top: 80px'"></div>
                                        <h3> {{ nomeUser }}</h3>
                                        <h4> {{ userCourseOrClass }}</h4>
                                        <h4> {{ userInstitution }}</h4>
                                        <h4> {{ userLocation }} </h4>

                                    </v-flex>

                                </v-layout>

                                <!--Total disciplinas-->
                                <v-layout v-if="isUser && user.identities && user.identities.isExplicador" class="ma-0">

                                    <v-flex xs12>

                                        {{ totalSubjects }} disciplinas

                                    </v-flex>

                                </v-layout>
                            </template>

                            <!--Pesquena identificacao-->
                            <v-layout v-else="">
                                <v-flex class="line-bottom mb-2">
                                    <v-layout>
                                        <!--Titulo da pagina-->
                                        <v-flex xs6 class="mt-1">
                                            <img style="float: left" class="ml-2" :src="user.photoURL" height="40" alt="">
                                            <span style="float: left" class="subheader">{{ user.name }}</span>
                                        </v-flex>

                                        <!--Botao voltar-->
                                        <v-flex xs6>
                                            <!--<v-btn round="" style="float: right" small="" @click="$router.go(-1)" class="green white&#45;&#45;text">Voltar</v-btn>-->
                                        </v-flex>
                                    </v-layout>
                                </v-flex>
                            </v-layout>

                            <!--New added for profile template-->
                            <template>
                                <div id="tabitem">
                                    <v-tabs
                                        slot="extension"
                                        v-model="currentItem"
                                        fixed-tabs
                                        color="blue"
                                        slider-color="yellow"
                                    >
                                        <v-tab
                                            v-for="item in items"
                                            :key="item"
                                            :href="'#tab-' + item"
                                            class="white--text resize-text-exp-xs-11"
                                            @click="onTabClick(item)"
                                        >

                                            {{ item }}

                                        </v-tab>
                                        <v-menu v-if="more.length" left bottom class="tabs__div exp-list">
                                            <a slot="activator" class="tabs__item resize-text-exp-xs-11">
                                                Mais
                                                <v-icon>arrow_drop_down</v-icon>
                                            </a>
                                            <v-list class="grey lighten-4">
                                                <v-list-tile
                                                    class="resize-text-exp-xs-11"
                                                    v-for="item in more"
                                                    :key="item"
                                                    @click="addItem(item)"
                                                >
                                                    {{ item }}
                                                </v-list-tile>
                                            </v-list>
                                        </v-menu>
                                    </v-tabs>
                                    <v-tabs-items v-model="currentItem">
                                        <v-tab-item
                                                v-for="item in items.concat(more)"
                                                :key="item"
                                                :id="'tab-' + item"
                                        >
                                            <v-card flat style="min-height: 600px">
                                                <v-card-text>
                                                    <v-flex class="text-xs-left">

                                                        <v-tooltip left class="hidden-xs-only">
                                                            <div style="float: right" slot="activator">
                                                                <img
                                                                        :src="img"
                                                                        style="height: 30px!important;width: 30px;border-radius: 100%;"
                                                                        class=""
                                                                        alt="">
                                                                <p style="margin: -30px 35px">{{ nomeUser }}</p>
                                                            </div>
                                                            <span v-if="isMyProfile">Meu Perfil</span>
                                                            <span v-else="">Estás no perfil do <b>{{ nomeUser }}</b></span>
                                                        </v-tooltip>

                                                        <h2>{{ item }}</h2>
                                                    </v-flex>
                                                    <!--{{ text }}-->

                                                    <!--Opcao de identidade-->
                                                    <template v-if="item === 'Identidade'">

                                                        <p class="text-xs-left mt-2">
                                                            A sua identidade é importante. E com base nela iremos te mostrar informações do seu interresse. Além disso,
                                                            nós vamos pedir para nos fornecer mais alguns dados dependendo da sua identidade.
                                                        </p>
                                                        <!--Opcoes de identidade-->
                                                        <v-layout v-if="isMyProfile" id="ideintity" justify-center >

                                                            <div style="width: 100%" >

                                                                <v-flex xs12 sm12>

                                                                    <v-card class="pb-5 white elevation-0">

                                                                        <!--Espaco do titulo-->
                                                                        <v-card-title class="white--text pa-0">

                                                                            <span class="headline ml-3">
                                                                                Minha identidade
                                                                            </span>

                                                                            <v-spacer></v-spacer>
                                                                            <div v-if="isMyProfile" class="white" style="margin-right:100px;border-radius: 100px; padding: 2px">
                                                                                <v-progress-circular
                                                                                        :size="circularSize"
                                                                                        :width="7"
                                                                                        :rotate="360"
                                                                                        title="Percentagem de preenchimento dos dados da identidade"
                                                                                        style="float: right;cursor: pointer"
                                                                                        :value="personalInfoFillPercent"
                                                                                        :color="personalInfoFillPercent > 0 ? 'green' : 'red'"
                                                                                        @click="toggle = !toggle"
                                                                                >
                                                                                    <span v-if="toggle">{{ personalInfoFillPercent }}</span>
                                                                                    <span v-else-if="true" title="Sua Identidade!" ><v-icon>done</v-icon></span>
                                                                                    <span v-else="" title="Clique em editar, para actualizar a sua disponibilidade">{{ personalInfoFillPercent }}</span>
                                                                                </v-progress-circular>
                                                                            </div>

                                                                        </v-card-title>

                                                                        <!--Botao gravar-->
                                                                        <v-layout>
                                                                            <!--Botao gravar identidade...-->
                                                                            <v-flex xs12 class="ma-4">
                                                                                <v-btn
                                                                                        title="Gravar todas as alteracoes"
                                                                                        flat=""
                                                                                        :outline="!$vuetify.breakpoint.xsOnly"
                                                                                        color=""
                                                                                        round=""
                                                                                        small=""
                                                                                        @click="onSave"
                                                                                        style="float: right"
                                                                                        v-if="loading"
                                                                                        :disabled="loading"
                                                                                        :loading="loading">
                                                                                    Gravar
                                                                                    <span slot="loader" class="custom-loader">
                                                            <v-icon light>cached</v-icon>
                                                        </span>
                                                                                </v-btn>

                                                                            </v-flex>
                                                                        </v-layout>

                                                                        <!--Opcao de estudante-->
                                                                        <v-layout>
                                                                            <v-flex xs12 sm3>
                                                                                <v-chip class="ml-3" :color="isEstudante ? yesColor : noColor " text-color="white">
                                                                                    <v-avatar>
                                                                                        <v-icon>check_circle</v-icon>
                                                                                    </v-avatar>
                                                                                    Estudante
                                                                                </v-chip>
                                                                            </v-flex>
                                                                            <v-flex xs12  sm4 offset-xs3>
                                                                                <v-switch
                                                                                        :label="isEstudante ? yes : no "
                                                                                        :color="isEstudante ? yesColor : noColor "
                                                                                        v-model="isEstudante"></v-switch>

                                                                            </v-flex>
                                                                        </v-layout>

                                                                        <!--Opcao de explicador-->
                                                                        <v-layout>
                                                                            <v-flex xs12 sm3>
                                                                                <v-chip class="ml-3" :color="isExplicador ? yesColor : noColor " text-color="white">
                                                                                    <v-avatar>
                                                                                        <v-icon>check_circle</v-icon>
                                                                                    </v-avatar>
                                                                                    Explicador
                                                                                </v-chip>
                                                                            </v-flex>
                                                                            <v-flex xs12  sm4 offset-xs3>
                                                                                <v-switch
                                                                                        :label="isExplicador ? yes : no "
                                                                                        :color="isExplicador ? yesColor : noColor "
                                                                                        v-model="isExplicador"></v-switch>

                                                                            </v-flex>
                                                                        </v-layout>

                                                                        <!--Opcao Outro-->
                                                                        <v-layout>
                                                                            <v-flex xs12 sm3>
                                                                                <v-chip class="ml-3" :color="isOther ? yesColor : noColor " text-color="white">
                                                                                    <v-avatar>
                                                                                        <v-icon>check_circle</v-icon>
                                                                                    </v-avatar>
                                                                                    Outro <span class="ml-4"></span>
                                                                                </v-chip>
                                                                            </v-flex>
                                                                            <v-flex xs12  sm4 offset-xs3>
                                                                                <v-switch
                                                                                        :label="isOther ? yes : no "
                                                                                        :color="isOther ? yesColor : noColor "
                                                                                        v-model="isOther"></v-switch>

                                                                            </v-flex>
                                                                        </v-layout>

                                                                    </v-card>

                                                                </v-flex>

                                                            </div>

                                                        </v-layout>

                                                    </template>

                                                    <!--Opcao de identidade-->
                                                    <template v-else-if="item === 'Sobre'">

                                                        <v-layout id="dadosPessoais" class="mt-5" justify-center>

                                                            <mobile-profile-personal-info :user="user" :circularSize="circularSize"></mobile-profile-personal-info>

                                                        </v-layout>

                                                    </template>

                                                    <template v-else-if="item === 'Disponibilidade'">

                                                        <!--Disponibilidade-->
                                                        <v-layout v-if="totalSubjects > 0" id="availability" class="mt-2" justify-center>

                                                            <mobile-profile-availability-info :user="user" :circularSize="circularSize"></mobile-profile-availability-info>

                                                        </v-layout>

                                                    </template>

                                                    <template v-else-if="item === 'Disciplinas'">

                                                        <p class="text-xs-left mt-2" v-if="isMyProfile">
                                                            Registe aqui todas as disciplinas que explica até 30 disciplinas.
                                                            E pode explicar até 3 niveis diferentes (Os explicadores Profissionais, nao tem restricoes. Podem explicador todos niveis que desejam e sao recomendados).
                                                            Lembre-se que a qualquer momento podemos alterar os limites conforme explicado nos <a
                                                                href="">termos e condições</a> da plataforma <b>Explicador</b>.
                                                        </p>
                                                        <!--Disciplinas-->
                                                        <v-layout id="disciplinas" class="mt-2" justify-center>

                                                            <mobile-profile-subjects-info :user="user" :circularSize="circularSize"></mobile-profile-subjects-info>

                                                        </v-layout>
                                                    </template>

                                                    <template v-if="item === 'Info. Profissionais'">

                                                        <p class="text-xs-left mt-2" v-if="isMyProfile">

                                                            Você é ou já foi Explicador, Professor ou Monitor em alguma instituição?...
                                                            Se for ou foi, não deixe de adicionar essas informaes, as pessoas gostam muito de explicadores desses,
                                                            tenha certeza que será procurado. E diversas instituições
                                                            irão recrutar aqui profissionais. Aproveite colocar a sua esperiência profissional.

                                                        </p>

                                                        <!--Dados Profissionais-->
                                                        <v-layout id="profissionalHistory"
                                                                  v-if="user && ((!user.identities || (user.identities && user.identities.isExplicador)) && (user.professionalHistory || isMyProfile))"
                                                                  class="mt-2" justify-center>

                                                            <mobile-profile-profissional-history-info :user="user" :circularSize="circularSize"></mobile-profile-profissional-history-info>

                                                        </v-layout>
                                                    </template>

                                                    <template v-if="item === 'Contactos'">
                                                        <p>Saiba quem solicita o contacto</p>

                                                        <!--Historico De contacto-->
                                                        <v-layout v-if="isMyProfile" id="contact" class="mt-2" justify-center>

                                                            <mobile-profile-contact-info :user="user" :circularSize="circularSize"></mobile-profile-contact-info>

                                                        </v-layout>

                                                    </template>

                                                    <template v-if="item === 'Educação'">
                                                        <!--Dados Academicos-->
                                                        <v-layout id="dadosAcademicos" class="mt-5" justify-center>

                                                            <mobile-profile-academic-info :user="user" :circularSize="circularSize"></mobile-profile-academic-info>

                                                        </v-layout>
                                                    </template>

                                                    <template v-if="item === 'Localização'">
                                                        <!--Localizacao-->
                                                        <v-layout id="localizacao" class="mt-5" justify-center>

                                                            <mobile-profile-location-info :user="user" :circularSize="circularSize"></mobile-profile-location-info>

                                                        </v-layout>
                                                    </template>

                                                    <template v-if="item === 'Confirmação de Email'">
                                                        <!--Seccao de confirmacao de email-->
                                                        <v-layout v-if="isMyProfile && !userVerified" id="email-verify" class="mt-5 mb-5 elevation-0">

                                                            <v-flex xs12 class="">
                                                                <v-expansion-panel class="elevation-0">
                                                                    <v-expansion-panel-content expand-icon="infos">

                                                                        <div slot="header">
                                                                        <span style="right: 5px;  margin-left: 10px; font-size: 13pt; color: darkblue">
                                                                            Voçê ainda não confirmou o seu email!
                                                                        </span>
                                                                            <br>
                                                                            <v-btn
                                                                                small=""
                                                                                round=""
                                                                                flat=""
                                                                                color="green"
                                                                                title="Clique para te enviarmos o link de verificacao"
                                                                                @click.stop="sendVerificationLink"
                                                                            >
                                                                                Enviar Link de confirmacao

                                                                                <v-icon>mail</v-icon>
                                                                            </v-btn>
                                                                            <!--<div class="mr-"></div>-->
                                                                            <v-btn
                                                                                    small=""
                                                                                    flat=""
                                                                                    color="red"
                                                                            >
                                                                                <v-icon>info</v-icon>
                                                                                Saiba mais
                                                                            </v-btn>
                                                                        </div>
                                                                        <v-card>
                                                                            <v-card-text>
                                                                                <h3>Porque confirmar email?</h3>
                                                                                <br>
                                                                                <br>
                                                                                <p>Utilizadores com email não confirmado:</p>
                                                                                <ul class="ml-5">
                                                                                    <li>
                                                                                        <p>Não serão confiados</p>
                                                                                    </li>
                                                                                    <li>
                                                                                        <p>Poderao não ser informados sobre muitas novidades</p>
                                                                                    </li>
                                                                                    <li>
                                                                                        <p>Serão ocultados ou serão os ultimos a ser alistados</p>
                                                                                    </li>
                                                                                </ul>
                                                                                <br>
                                                                                <!--<p  class="pa-2" style="border-left: 2px solid blueviolet; background: rgba(200,200,200,.2)">A confirmação de emai é importante, porque dá certeza as pessoas que te procuram de que os seus dados de contactos são válidos.</p>-->
                                                                                <br>
                                                                                <!--<h3>Que dados confirmar?</h3>-->
                                                                                <!--<br>-->
                                                                                <!--<p>Por enquanto confirme apenas o seu email! é bom que faças isso urgente para não perder nada.</p>-->
                                                                                <!--<b>Confirme o seu email, clique no botao acima para te enviarmos para o seu email o link de verificação!</b>-->

                                                                                <br>
                                                                                <br>
                                                                                <p class="pa-2" style="border-left: 2px solid blueviolet; background: rgba(200,200,200,.1)">Pode <b style="cursor: pointer" @click.prevent="sendVerificationLink">Clicar aqui</b>,
                                                                                    te enviaremos o link por email
                                                                                </p>
                                                                            </v-card-text>
                                                                        </v-card>
                                                                    </v-expansion-panel-content>
                                                                </v-expansion-panel>
                                                            </v-flex>

                                                        </v-layout>

                                                    </template>

                                                </v-card-text>
                                            </v-card>
                                        </v-tab-item>
                                    </v-tabs-items>
                                </div>
                            </template>

                        </v-card-text>

                    </v-flex>

                </v-layout>

            </v-flex>

        </v-layout>


    </v-container>

    <v-layout v-else="" class="text-xs-center">
            <v-flex xs12 style="margin-top: 10%;">
                <v-card-media
                    :src="auth_img3_100x100"
                    class="img-radius"
                    style="cursor: pointer"
                >
                </v-card-media>

                <br>
                <v-progress-circular indeterminate color="blue"></v-progress-circular>
                <h2>Carregando o perfil . . . </h2>

                <small>Se demorar, clique em voltar</small>
                <p><v-btn router :to="{name: 'Home'}" class="azul-principal white--text">Voltar</v-btn></p>
            </v-flex>
        </v-layout>

</template>

<script>
    import { eventBus } from './../../main'
    export default {
        props: ['user', 'name'],
        data: () => ({
            showPresentation: true,
            isStarting: true,
            showDialog: false,
            showSubjectsEditDialog: false,
            isExplicador: false,
            isEstudante: false,
            isOther: false,
            available: true,
            yesColor: 'blue',
            noColor: 'grey',
            no: 'Não',
            yes: 'Sim',
            isThis: false,
            circularSize: 40,
            toggle: true,
            loadingProcess: '',
            auth_img3_100x100: 'https://user-images.githubusercontent.com/18400142/39090637-e2e747fc-45e4-11e8-8675-0a2ab6fb4013.png',

            //New added for profil
            currentItem: 'tab-Sobre',
            items: [],
            more: [],
            text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.'
            //Fim
        }),
        mounted () {

            this.fillTabsLists ()
            //Verifica automaticamente se eh preciso actualizar a foto ou nao.
            this.$store.dispatch('verifyIfIsDefaultPhoto')

            //Para nao actualizar automaticamente a identidade no inicio
            let vm = this

            setTimeout(function(){

                if (vm.route.name === 'Profile')
                {
                    vm.saveNewView();
                    vm.inicializeUserInfo () //Inicializacao da Identificacao so user
                    vm.isStarting = true
                }

            }, 10000); //Se o user leva pelo menos 10seg a
            // visualizar o perfil de alguem, entao ele ganha mais uma visualizacao...x`

            //Evento emitido no Componente App.vue
            eventBus.$on('showDialogSujects', ()=> {

                this.showSubjectsEditDialog = true

            })

            //evento emitido no dialogs/OtherProfileEditSubjectDialog.vue
            eventBus.$on('resetShowDialogSujects', () => {

                this.showSubjectsEditDialog = false

            })

            this.inicializeUserInfo () //Inicializacao dos dados do user

        },

        created () {

            this.scrollTo(0)

            //Verificar aqui se eh o primeiro login, para parabenizar.
            if (this.isUserAuth && this.userAuth !== null && !this.userAuth.updated_at)
            {
                let vm = this
                setTimeout(function () { vm.mostrarDialog('Welcome') }, 5000)

            }
            else if (this.isUserAuth && this.isConfirmMailSent)
            {
                let vm = this
                setTimeout(function(){ vm.mostrarDialog() }, 20000);

            }


        },
        computed: {
            options () { //usado para fazer o scroll
                return {
                    duration: 1000,
                    offset: 0,
                    easing: 'easeInQuint',
                    controlRoute: false,
                }
            },
            isUser () {
                return this.user !== undefined && this.user !== null
            },
            personalInfoFillPercent ()
            {
                return this.isUserAuth && this.userAuth.hasOwnProperty('identities') ? 100 : 0
            },
            smallImageUrl ()
            {
                return this.$store.getters.getSmallImageUrl
            },
            img () {

                if (this.isUserAuth && this.isMyProfile) return this.$store.getters.getImg

                return this.isUser && this.user.photoURLServer ? this.smallImageUrl + '' + this.user.photoURLServer :
                    this.isUser ? this.user.photoURL : '//ssl.gstatic.com/s2/oz/images/sge/grey_silhouette.png'
            },
            nomeUser() {
                return this.isUser && this.user.name !== null && this.user.name !== '' ? this.user.name : 'Sem Nome'
            },
            totalSubjects ()
            {
                let subjects = this.isUser && this.user.Explicador && this.user.Explicador.subjectDetails ? this.user.Explicador.subjectDetails : []
                let total = 0
                subjects.forEach((sbs) => {
                    total += sbs.subjects.length
                })
                return total
            },
            userLocation() {

                const negative = 'Ainda nao registou a localizacao'

                if (!this.isUser || !this.user.Locations) return negative

                let userBairro = this.user.Locations.bairro
                let isFromMaputoCity = this.user.Locations.isFromMaputoCity
                let isFromMaputoProvince = this.user.Locations.isFromMaputoProvince
                let district = this.user.Locations.district
                let locality = this.user.Locations.locality

                return (isFromMaputoCity || isFromMaputoProvince) && userBairro !== null && userBairro
                        ? userBairro : (locality)
                        ? locality : district
                        ? district : negative

            },
            userInstitution () {

                const defaultTitle = 'Sem Instituicao'

                if (!this.isUser || !this.user.Educations) return defaultTitle

                let userEd = this.user.Educations

                let inst = userEd.level === 'Primário' ? userEd.userSchool : defaultTitle
                inst = userEd.level === 'Secundário' ? userEd.userSchool : inst
                inst = userEd.level === 'Técnico' ? userEd.institution : inst
                inst = userEd.level === 'Superior' ? userEd.institution : inst
                inst = userEd.level === 'Outro' ? userEd.institution : inst

                return inst
            },
            userCourseOrClass () {

                const defaultTitle = ''

                if (!this.isUser || !this.user.Educations) return defaultTitle

                let userEd = this.user.Educations

                let inst = userEd.level === 'Primário' ? userEd.userClass : defaultTitle
                inst = userEd.level === 'Secundário' ? userEd.userClass : inst
                inst = userEd.level === 'Técnico' ? userEd.courseNivel : inst
                inst = userEd.level === 'Superior' ? userEd.courseNivel : inst
                inst = userEd.level === 'Outro' ? userEd.userSchool : inst

                return inst
            },
            userAuth () {
                return this.$store.getters.userAuth
            },


            isUserAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },
            isMyProfile () {
                return this.isUserAuth && this.isUser && this.user.id === this.userAuth.id
            },
            userVerified () {
                return this.user.emailVerified
            },
            gender () {

                const negative = 'Sexo nao definido'

                if (!this.isUser) return negative

                let genero = this.user.gender

                return genero && genero !== null && genero && genero !== '' ? genero : negative


            },
            userFaculty () {

                const negative = 'Sem Faculdade'

                if (!this.isUser) return negative

                let ed = this.user.Educations
                return ed && ed.faculty ?  'Faculdade de ' + ed.faculty : negative
            },
            userCourse () {

                const negative = 'Sem Curso'

                if (!this.isUser) return negative

                let ed = this.user.Educations

                return ed && ed.course ? 'Curso de ' + ed.course : negative

            },
            userDepartment () {

                const negative = 'Sem Departamento'

                if (!this.isUser) return negative

                let ed = this.user.Educations

                return ed && ed.department ? ed.department : negative

            },
            userNivel () {

                const negative = 'Sem Nivel'

                if (!this.isUser) return negative

                let ed = this.user.Educations

                return ed && ed.nivel ? ed.nivel : negative


            },
            hasSubjects () {

                if ( !this.isUser ) return false

                return this.user && this.user.Explicador && this.user.Explicador.subjectDetails && this.user.Explicador.subjectDetails.length
            },

            route() {
                return this.$route
            },
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },

            isConfirmMailSent () {
                return this.$store.getters.getConfirmMailSent
            },

        },
        watch: {

            route(value) {

                window.location.reload(false);

            },
            loading(value) {
                if (!value && !this.error && this.isThis) {

                    this.mostrarDialog()
                    this.isThis = false

                }
                if (this.showProcess && !value && !this.error) {

                    let text = "A sua identidade foi actualizada com sucesso!"
                    let color = 'green'
                    let timeout = 5000
                    eventBus.$emit('showSnackbar', text, color, timeout); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar
                    this.showProcess = false
                }
            },
            isExplicador(value)
            {
                this.onSave()
            },
            isEstudante(value)
            {
                this.onSave()
            },
            isOther(value)
            {
                this.onSave()
            }
        },
        methods: {

            fillTabsLists ()
            {
                if (this.$vuetify.breakpoint.xsOnly) //Verificando se eh dispositivo menor
                {
                    this.items = ['Sobre', 'Disciplinas']
                    this.more =['Localização', 'Educação', 'Identidade', 'Info. Profissionais', 'Disponibilidade', 'Contactos', 'Confirmação de Email']
                }
                else
                {
                    this.items = ['Sobre', 'Educação', 'Localização', 'Disciplinas']
                    this.more =['Identidade', 'Info. Profissionais', 'Disponibilidade', 'Contactos', 'Confirmação de Email']
                }

                if (!this.isMyProfile)
                {
                    this.more.splice(this.more.indexOf('Identidade'), 1);
                    this.more.splice(this.more.indexOf('Confirmação de Email'), 1);

                    if (this.isUser && !this.user.professionalHistory)
                    {
                        this.more.splice(this.more.indexOf('Info. Profissionais'), 1);
                    }
                }
                if (this.isMyProfile && this.userVerified) //se for meu perfil e estiver verificado pode nao add.
                {
                    this.more.splice(this.more.indexOf('Confirmação de Email'), 1);
                }

            },
            //new added for profile

            addItem (item) {
                const removed = this.items.splice(0, 1)
                this.items.push(
                    ...this.more.splice(this.more.indexOf(item), 1)
                )
                this.more.push(...removed)
                this.$nextTick(() => { this.currentItem = 'tab-' + item })
            },
            //end
            onTabClick (val)
            {
                if(window.scrollY < 300 && this.$vuetify.breakpoint.mdAndUp)
                {
                    this.scrollTo('472')
                }
            },
            scrollTo (el) {

                if (isNaN(el))
                {
//                    Scroll to a certain element
                    document.querySelector(el).scrollIntoView({
                        behavior: 'smooth'
                    });
                }
                else
                {
                    window.scroll({
                        top: el,
                        left: 10,
                        behavior: 'smooth'
                    });
                }




            },

            saveNewView ()
            {
                if (this.route.name === 'Profile' && this.isUser && !this.isMyProfile)
                {
                    //Depois de 1min a se visualizar o perfil... - done
                    //Verificamos se essa visualizacao de hoje ja foi adicionada na base de dados
                    //adicionamos mais um no numero de visualizacoes...
                    //Gravamos na base de dados

                    let user = this.user;

                    let whoSawTheProfile = this.isUserAuth ? this.userAuth.id : 'guest'; //Buscamos o id do user que viu o perfil ou apenas visitante

                    let today = new Date() //data da visualizacao


                    //verificar se ja tinha recebido visualizacoes
                    if (user.Profile && user.Profile.views)
                    {
                        let userViews = user.Profile.views

                        let recentViewObject = userViews[userViews.length - 1] //Ultima visualizacao
                        let viewsDate = new Date(recentViewObject.date) //Data da ultima visualizacao

                        let d1 = new Date(viewsDate.getFullYear(), viewsDate.getMonth(), viewsDate.getDate()); //data da visualizacao
                        let d2 = new Date(today.getFullYear(), today.getMonth(), today.getDate()); //data de hoje

                        if (d1.getTime() === d2.getTime()) //se as datas forem as mesmas, precisamos verificar se esse ainda nao foi add.
                        {
                            if ( !(recentViewObject.usersId.includes(whoSawTheProfile)) || whoSawTheProfile === 'guest')
                            {
                                //Ja existem visualizacoes de hhoje
                                //pode gravar a nova visualizacao

                                recentViewObject.usersId.push(whoSawTheProfile)

                                this.verifyViewsAndSendEmailNotification (userViews)
                            }

                        }
                        else //forem diferentes entao, gravamos a primeira visualizacao de hoje.
                        {
                            userViews.push({
                                date: today,
                                usersId: [whoSawTheProfile],
                            })

                            this.verifyViewsAndSendEmailNotification (userViews)
                        }

                        //verificar aqui para notificar por email


                    }
                    else //ainda nao possui visualizacoes...
                    {
                        //Adicionamos a primeira visualizacao
                        //Se tiver recuperamos das ja possui

                        user.Profile = {
                            views: [
                                {
                                    date: today,
                                    usersId: [whoSawTheProfile],
                                }
                            ]
                        }

                    }


                    user.isNotMyProfile = 'isNotMyProfile' //indicador para nao actualizar o userAuth
                    this.$store.dispatch('updateOnlineUserData', user)


                }

            },

            onSave ()
            {

                if (this.isStarting) return

                if (!this.isUser) return alert('voce nao esta auth')//se o user nao estiver autenticado ou nao for explicador nao grava

                this.user.identities = {

                    isExplicador: this.isExplicador ? this.isExplicador : false,
                    isStudent: this.isEstudante ? this.isEstudante : false,
                    isOther: this.isOther ? this.isOther : false,

                }

                this.showProcess = true
                this.$store.dispatch('updateOnlineUserData', this.user)
                this.$store.dispatch('getAllUsers')
            },

            inicializeUserInfo ()
            {

                if (!this.isUser) return

                this.isExplicador = this.user.identities && this.user.identities.isExplicador
                this.isEstudante = this.user.identities && this.user.identities.isStudent
                this.isOther = this.user.identities && this.user.identities.isOther

            },


            verifyViewsAndSendEmailNotification ( userViews )
            {
                let total = 0

                userViews.forEach((uv) => {

                    total += uv.usersId.length

                })

                if (total % 10 !== 0) return

                let message = 'Parabéns ' + this.user.name + ', O seu perfil do explicador possui agora ' + total + ' visualizações.'
                    + ' Mantenha os seus dados do perfil actualizados para que as pessoas vejam as suas informações recentes. Cumprimentos!.'
                let subject = 'Parabens pelas visualizações  no Explicador'

                let email = this.user.email ? this.user.email : 'joseeugenioseie@hotmail.com'

                let emailObject = {
                    message: message,
                    subject: subject,
//                        to: 'joseeugenioseie@gmail.com',
                    emails: [email]
                }

                this.$store.dispatch('sendMail', emailObject)


            },

            fechandoDialog ()
            {
                this.showDialog = false
            },

            sendVerificationLink () {
                this.isThis = true
                this.$store.dispatch('sendVerificationLink')
            },
            mostrarDialog (value) {

                let title = ''
                let message = ''
                let btn1 = ''
                let btn2 = 'Ok'

                if (value && value === 'Welcome')
                {
                    title = 'Seja bem vindo a plataforma dos explicadores'
                    message = 'Esperamos que por meio desta plataforma consigas encontrar ajuda ou ajudar outras pessoas. ' +
                        'vais encontrar aqui, varios explicadores qualificados ou pode se tornar explicador Por registar apenas as disciplinas que Explicas. ' +
                        'Se Desejar pode alterar a sua disponibilidade, tornar-se anónimo ou ainda Estar Visivel, disponivel para ser procurado. ' +
                        'Preencha os dados do seu perfil, de modo a completar 100% em cada parte. Obrigado. ' +
                        '' +
                        ''
                    btn1 = ''
                    btn2 = 'Ok'

                    //actualizar os dados do user no mostrar dialog
                    let user = this.userAuth
                    user.updated_at = new Date()
                    this.$store.dispatch('updateOnlineUserData', user)
                }
                else {

                    title = 'Email de confirmação enviado com sucesso .. .'
                    message = 'Acabamos de te enviar um email. Acesse a caixa de entrada na sua conta de email para confirmar. Te enviamos um link de confirmação, e só clicar. Cumprimentos.'
                    btn1 = ''
                    btn2 = 'Ok'

                }

                /**
                 * Primeiro param: true: significa abrir a dialog
                 * Segundo param:  title: o titulo a mostrar
                 * Terceiro param:  msg: a mensagem a mostrar
                 */
                this.$store.dispatch('togleConfirmDialog', {show: true, value: value, title: title, message: message,btn1: btn1,btn2: btn2})
                this.$store.dispatch('confirmationMailSet', false) //mostra que o email de confirmacao ja foi enviado

            },

        },
    }
</script>
<style scoped="">

    @media screen and (max-width: 600px){
        .card-img-margin {
            margin-top: 6.2em!important;
        }
        h4, h5, h6 {
            font-size: 11px!important;
        }
        h2, h3 {
            font-size: 14px;
        }

    }
    div.toolbar__content {
        height: 0px!important;
    }
    .edit-photo-icon {
        top: -20px;
        right: -20px;
        position: absolute;
    }

    .explicador-bar-fixed {
        z-index: 0;
    }
    .explicador-edit-item {
        float: right;
        cursor: pointer
    }
    .icon {
        font-size: 18px;
    }
    .img-radius {
        width: 50px;
        height: 50px!important;
    }
    .container {
        padding: 0px;
    }
    .resize-text-exp-xs-11 a {
        height: 2px!important;
        font-size: 10px!important;
    }

</style>


