<template>

        <div>
            <!--&lt;!&ndash;Botao que leva ao home&ndash;&gt;-->
            <!--<v-btn-->
                <!--v-if="$vuetify.breakpoint.width < 1200"-->
                <!--class="mt-5 elevation-0 ml-1"-->
                 <!--fab-->
                 <!--bottom-->
                 <!--left-->
                 <!--color="blue"-->
                 <!--dark-->
                 <!--fixed-->
                 <!--small=""-->
                 <!--to="/"-->
                 <!--slot="activator"-->
            <!--&gt;-->
                <!--<v-icon>home</v-icon>-->
            <!--</v-btn>-->

            <!--Botao que some para cima.-->
            <v-btn
                v-if="showGotoTop"
                fab
                bottom
                right
                color="blue"
                dark
                fixed
                :style="$vuetify.breakpoint.mdAndDown ? 'right: 8px!important;' : 'right: 120px!important;'"
                small=""
                @click="scrollTo(0)"
            >
                <v-icon>keyboard_arrow_up</v-icon>
            </v-btn>

        </div>

</template>

<script>
    export default {
        data ()
        {
            return {
                showGotoTop: false,
            }
        },
        created () {
            window.addEventListener('scroll', this.handleScroll);
        },
        destroyed () {
            window.removeEventListener('scroll', this.handleScroll);
        },
        methods: {
            handleScroll (event) {
                this.showGotoTop = window.scrollY > 300
            },
            scrollTo (top)
            {
                window.scroll({
                    top: top,
                    left: 10,
                    behavior: 'smooth'
                })
            }
        }

    }
</script>


